#social-icons{
	align-self: flex-end;
	width: 22%;
}
#information-heading{
	font-size: 2em;
}
#information-text{
	cursor: default;
	width: 90%;
	font-size: 1.2em;
	text-align: right;
	line-height: 1.7;
}
.contact-container{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.contact-left-half{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50%;
	height: 100vh;
}
.social-media-container{
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	width: 65%;
	user-select: none;
}
.contact-right-half{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50%;
	height: 100vh;
}
.form-container{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 90%;
	height: 90%;
	padding-bottom: 20px;
	background: #C4ADBF;
	user-select: none;
}
/*FOR PHONES*/
@media (max-width: 520px) {
	#information-text{
		font-size: 1em!important;
		text-align: center!important;
	}
	#social-icons{
		width: 30%!important;
	}
	.contact-container{
		flex-direction: column;
	}
	.contact-left-half{
		height: 100px;
		width: 75%;
		margin-right: 150px;
		margin-bottom: 10px;
	}
	.contact-right-half{
		height: 60vh;
		width: 100%;
	}
	.information-container{
		padding: 100px;
	}
	.social-media-container{
		width: 100%;
		height: 50px;
		justify-content: flex-end;
		margin-left: 20px;
	}
	.form-container{
		width: 100%;
		padding-right: 10px;
		padding-top: 20px;
		margin-top: 0px;
		height: 400px;
		box-shadow: 0 5px 20px rgba(0,0,0,.25);
	}
}