.navbar-container{
	position: absolute;
	right: 0;
	margin: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background: white;
	width: 20%;
	height: 100%;
	z-index: 2;
	box-shadow: 0 5px 20px rgba(0,0,0,.25);
}
.navbar-list{
	list-style: none;
	font-size: 1.5em;
}
p{
	cursor: pointer;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	user-select: none;
}
/*FOR PHONES*/
@media (max-width: 520px) {
	.navbar-container{
		width: 100%;
		align-items: center;
		padding-right: 50px;
		height: 300px;
	}
	.navbar-list{
		font-size: 1em; 
	}
}